@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,700');

.solar-system {
  transform-style: preserve-3d;
  height: 100vh;  // 각 섹션의 높이를 동일하게 설정하여 일관성 유지
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  background: black;
}

label {
  cursor: pointer;
  display: block;
  position: absolute;
  left: 90px;
  z-index: 10; // 클릭 가능하게 z-index 조정
  color: white;

  &:after {
    display: block;
    width: 12px;
    height: 12px;
    border: 2px solid white;
    border-radius: 60px;
    content: '';
    position: absolute;
    top: -4px;
    left: -4px;
  }

  &:hover {
    h2,
    h3 {
      opacity: 1;
    }
  }
}

input[type="radio"] {
  appearance: none;
  pointer-events: auto;
}